import React from 'react';
import Landingpage from './landingpage';
import {Switch,Route} from 'react-router-dom';
import Aboutme from './aboutme';
import Resume from './resume';
import Contact from './contact';
import Project from './project';

const Main = () => (
    <Switch>
        <Route exact path="/Portfoliowebsite/" component={Landingpage}/>
        <Route exact path="/Portfoliowebsite/aboutme" component={Aboutme}/>
        <Route exact path="/Portfoliowebsite/resume" component={Resume}/>
        <Route exact path="/Portfoliowebsite/contact" component={Contact}/>
        <Route exact path="/Portfoliowebsite/projects" component={Project}/>
    </Switch>
)

export default Main;